import React from 'react'
import AppLayout from '../components/AppLayout'
import Head from 'next/head'
import {Form, Input, Checkbox,Button} from 'antd'

const SignUp = () =>{

  const term = ""; 
  const onSubmit = ()=>{}
  const onChangeId = ()=>{}
  const onChangeNick = ()=>{}
  const onChangePassword = ()=>{}          
  const onChangeChk = ()=>{}
  const onChangeTerm = ()=>{}


    return(
        
        <>
        <Head>
            <title>NodeBird</title>
            <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/antd/3.16.2/antd.css" />
        </Head>
            <Form onSubmit={onSubmit} style={{padding : 10}}>
                <div>
                    <label htmlFor="user-id">아이디</label>
                    <br />
                    <Input name="user-id" required onChange={onChangeId}/>
                </div>
                <div>
                    <label htmlFor="user-nick">닉네임</label>
                    <br />
                    <Input name="user-nick" required onChange={onChangeNick}/>                
                </div>
                <div>
                    <label htmlFor="user-password">비밀번호</label>
                    <br />
                    <Input name="user-password" required onChange={onChangePassword}/>
                </div>
                <div>
                    <label htmlFor="user-chk">비밀번호</label>
                    <br />
                    <Input name="user-chk" required onChange={onChangeChk}/>
                </div>
                <div>
                    <Checkbox name="user-term" value={term} onChange={onChangeTerm}>동의 합니다.</Checkbox>
                </div>
                <div>
                    <Button type="primary" htmlType="submit">가입하기</Button>
                </div>
            </Form>
        
         </>

      
    )
}

export default  SignUp; 